import React, { useEffect, useRef } from 'react';
import { useSelector } from 'react-redux';
import './AnimatedObject.css';

function AnimatedObject() {
  const scrollProgress = useSelector((state) => state.scroll.scrollProgress);
  const scrollY = useSelector((state) => state.scroll.scrollY);
  const objectRef = useRef(null);

  useEffect(() => {
    let animationId;
    
    const animate = () => {
      if (objectRef.current) {
        const time = Date.now() * 0.001;
        
        // Get viewport dimensions
        const viewportWidth = window.innerWidth;
        const viewportHeight = window.innerHeight;
        
        // Responsive logo size
        let logoSize = 120; // Base logo size
        if (viewportWidth <= 480) {
          logoSize = 80;
        } else if (viewportWidth <= 768) {
          logoSize = 100;
        }
        
        let scale, opacity, blur;
        let top;
        
        if (scrollProgress <= 0.05) {
          // 0% -> 5%: scale up from initial position
          const t = scrollProgress / 0.05; // 0..1
          
          // Aggressive easing function for instant response to fast scrolling
          const smoothT = Math.pow(t, 0.3); // Very aggressive easing - responds immediately
          
          scale = 1.4 + smoothT * 0.9; // 1.4 -> 2.3
          opacity = 1;
          blur = 0;
          
          // Start at 20% height, move down 50px
          top = viewportHeight * 0.2 + smoothT * 50;
          
        } else {
          // 5% -> 100%: stay at center, scale down and fade to background
          const t = (scrollProgress - 0.05) / 0.95; // 0..1
          scale = 2.3 - t * 0.7; // 2.3 -> 1.6
          
          // Fade to background so content is visible
          if (t <= 0.2) {
            // First 20%: stay visible
            opacity = 1;
            blur = 0;
          } else {
            // After 20%: fade to background
            const fadeT = (t - 0.2) / 0.8; // 0..1
            opacity = 1 - fadeT * 0.85; // 1 -> 0.15 (fade to 15% opacity)
            blur = fadeT * 5; // 0 -> 5px blur
          }
          
          // Stay at center position
          top = viewportHeight * 0.2 + 50 - t * 20; // Move slightly up
        }
        
        // Add subtle floating animation
        const floatY = Math.sin(time * 0.5) * 4;
        
        // Apply positioning and transformations
        objectRef.current.style.position = 'absolute';
        objectRef.current.style.right = '20px';
        objectRef.current.style.top = `${top + floatY}px`;
        objectRef.current.style.left = 'auto';
        objectRef.current.style.transform = `scale(${scale})`;
        objectRef.current.style.opacity = opacity;
        
        // Dynamic z-index based on scroll position - keep logo behind content
        let zIndex;
        if (scrollProgress <= 0.05) {
          zIndex = 4000; // Moving phase - normal z-index
        } else if (scrollProgress <= 0.25) {
          zIndex = 5000; // Centered and visible - medium z-index
        } else {
          zIndex = -1; // Background phase - negative z-index so content is always visible
        }
        objectRef.current.style.zIndex = zIndex;
        
        objectRef.current.style.filter = `blur(${blur}px)`;
      }
      animationId = requestAnimationFrame(animate);
    };
    
    animate();
    
    // Handle window resize
    const handleResize = () => {
      // Animation will automatically recalculate on next frame
    };
    
    window.addEventListener('resize', handleResize);
    
    return () => {
      if (animationId) {
        cancelAnimationFrame(animationId);
      }
      window.removeEventListener('resize', handleResize);
    };
  }, [scrollProgress, scrollY]);

  return (
    <div className="animated-object">
      <div ref={objectRef} className="tech-logo">
        <div className="logo-container">
          {/* Central Technology Circle */}
          <div className="tech-circle">
            <div className="circle-inner">
              <div className="tech-symbol">AT</div>
            </div>
          </div>
          
          {/* Technology Rings */}
          <div className="tech-rings">
            <div className="ring ring-1"></div>
            <div className="ring ring-2"></div>
            <div className="ring ring-3"></div>
          </div>
          
          {/* Orbital Technology Dots */}
          <div className="orbit-dots">
            {[...Array(8)].map((_, i) => (
              <div 
                key={i} 
                className="orbit-dot" 
                style={{
                  animationDelay: `${i * 0.3}s`,
                  transform: `rotate(${i * 45}deg) translateX(50px)`
                }}
              />
            ))}
          </div>
          
          {/* Technology Lines */}
          <div className="tech-lines">
            <div className="line line-1"></div>
            <div className="line line-2"></div>
            <div className="line line-3"></div>
            <div className="line line-4"></div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default AnimatedObject;
