import React, { useEffect, useRef } from 'react';
import { useDispatch } from 'react-redux';
import Lenis from 'lenis';
import { setScrollY, setScrollProgress, setIsScrolling } from './store/scrollSlice';
import Hero from './components/Hero';
import Services from './components/Services';
import Clients from './components/Clients';
import Contact from './components/Contact';
import AnimatedObject from './components/AnimatedObject';
import ScrollProgress from './components/ScrollProgress';
import './App.css';

function App() {
  const dispatch = useDispatch();
  const lenisRef = useRef(null);

  useEffect(() => {
    // Initialize Lenis with enhanced settings
    const lenis = new Lenis({
      duration: 1.2,
      easing: (t) => Math.min(1, 1.001 - Math.pow(2, -10 * t)),
      direction: 'vertical',
      gestureDirection: 'vertical',
      smooth: true,
      mouseMultiplier: 1,
      smoothTouch: false,
      touchMultiplier: 2,
      infinite: false,
      syncTouch: true,
      touchInertiaMultiplier: 35,
      wheelMultiplier: 1,
    });

    lenisRef.current = lenis;

    // Enhanced Lenis scroll event with more responsive updates
    function raf(time) {
      lenis.raf(time);
      requestAnimationFrame(raf);
    }

    requestAnimationFrame(raf);

    // Update Redux store with scroll position and add cursor effects
    lenis.on('scroll', ({ scroll, limit, velocity, direction, progress }) => {
      dispatch(setScrollY(scroll));
      dispatch(setScrollProgress(progress));
      dispatch(setIsScrolling(Math.abs(velocity) > 0.1));
      
      // Add dynamic cursor effects based on scroll
      updateCursorEffects(progress, velocity);
    });

    // Add custom cursor
    const addCustomCursor = () => {
      const cursor = document.createElement('div');
      cursor.className = 'custom-cursor';
      document.body.appendChild(cursor);

      const cursorFollower = document.createElement('div');
      cursorFollower.className = 'cursor-follower';
      document.body.appendChild(cursorFollower);

      document.addEventListener('mousemove', (e) => {
        cursor.style.left = e.clientX + 'px';
        cursor.style.top = e.clientY + 'px';
        
        setTimeout(() => {
          cursorFollower.style.left = e.clientX + 'px';
          cursorFollower.style.top = e.clientY + 'px';
        }, 100);
      });

      document.addEventListener('mouseenter', () => {
        cursor.style.opacity = '1';
        cursorFollower.style.opacity = '1';
      });

      document.addEventListener('mouseleave', () => {
        cursor.style.opacity = '0';
        cursorFollower.style.opacity = '0';
      });
    };

    addCustomCursor();

    return () => {
      lenis.destroy();
      // Clean up cursor elements
      const cursor = document.querySelector('.custom-cursor');
      const cursorFollower = document.querySelector('.cursor-follower');
      if (cursor) cursor.remove();
      if (cursorFollower) cursorFollower.remove();
    };
  }, [dispatch]);

  // Function to update cursor effects based on scroll
  const updateCursorEffects = (progress, velocity) => {
    const cursor = document.querySelector('.custom-cursor');
    const cursorFollower = document.querySelector('.cursor-follower');
    
    if (cursor && cursorFollower) {
      const scale = 1 + progress * 0.5;
      const blur = Math.abs(velocity) * 2;
      
      cursor.style.transform = `scale(${scale})`;
      cursorFollower.style.transform = `scale(${scale * 0.8})`;
      cursorFollower.style.filter = `blur(${blur}px)`;
    }
  };

  return (
    <div className="App">
      <ScrollProgress />
      <AnimatedObject />
      <Hero />
      <Services />
      <Clients />
      <Contact />
    </div>
  );
}

export default App;
