import React, { useRef } from 'react';
import { motion, useInView } from 'framer-motion';
import './Services.css';

function Services() {
  const ref = useRef(null);
  const isInView = useInView(ref, { once: true, margin: "-100px" });

  const services = [
    {
      title: "Mobile Application Development",
      description: "We create native and cross-platform mobile applications that deliver exceptional user experiences across iOS and Android platforms.",
      features: [
        "Native iOS & Android Development",
        "React Native Cross-Platform",
        "Flutter Development",
        "UI/UX Design Integration",
        "App Store Optimization",
        "Performance Optimization"
      ],
      icon: "",
      color: "#6366f1"
    },
    {
      title: "Web Development",
      description: "Modern, responsive web applications built with cutting-edge technologies to ensure scalability, performance, and user engagement.",
      features: [
        "React & Next.js Development",
        "Full-Stack Solutions",
        "Progressive Web Apps",
        "E-commerce Platforms",
        "API Development",
        "Cloud Integration"
      ],
      icon: "",
      color: "#a855f7"
    }
  ];

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.2
      }
    }
  };

  const itemVariants = {
    hidden: { opacity: 0, y: 50 },
    visible: {
      opacity: 1,
      y: 0,
      transition: {
        duration: 0.8,
        ease: "easeOut"
      }
    }
  };

  return (
    <section ref={ref} className="services section">
      <div className="section-content">
        <motion.div
          className="services-header"
          initial={{ opacity: 0, y: 30 }}
          animate={isInView ? { opacity: 1, y: 0 } : { opacity: 0, y: 30 }}
          transition={{ duration: 0.8 }}
        >
          <h2 className="section-title">Our Services</h2>
          <p className="section-subtitle">
            We specialize in creating digital solutions that drive business growth and enhance user experiences.
          </p>
        </motion.div>

        <motion.div
          className="services-grid"
          variants={containerVariants}
          initial="hidden"
          animate={isInView ? "visible" : "hidden"}
        >
          {services.map((service, index) => (
            <motion.div
              key={index}
              className="service-card"
              variants={itemVariants}
              whileHover={{ 
                scale: 1.02,
                transition: { duration: 0.3 }
              }}
            >
              <div className="service-icon" style={{ backgroundColor: service.color }}>
                <span>{service.icon}</span>
              </div>
              
              <h3 className="service-title">{service.title}</h3>
              <p className="service-description">{service.description}</p>
              
              <ul className="service-features">
                {service.features.map((feature, featureIndex) => (
                  <li key={featureIndex} className="feature-item">
                    <span className="feature-check"></span>
                    {feature}
                  </li>
                ))}
              </ul>
              
              <div className="service-cta">
                <button className="service-btn" style={{ borderColor: service.color, color: service.color }}>
                  Learn More
                </button>
              </div>
            </motion.div>
          ))}
        </motion.div>

        <motion.div
          className="services-cta"
          initial={{ opacity: 0, y: 30 }}
          animate={isInView ? { opacity: 1, y: 0 } : { opacity: 0, y: 30 }}
          transition={{ duration: 0.8, delay: 0.4 }}
        >
          <h3>Ready to start your project?</h3>
          <p>Let's discuss how we can bring your vision to life with our expertise in mobile and web development.</p>
          <button className="btn-primary">Start Your Project</button>
        </motion.div>
      </div>
    </section>
  );
}

export default Services;
